import { Component } from '@angular/core';
import { Url } from 'url';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
 getUrl:any;
 clickHandelerFirst(FirstUrl:string)
  {
    window.open(FirstUrl).document.all;
  }
  clickHandelerSecond(secondUrl:any)
  {
    window.open(secondUrl)
  }
  clickHandelerTherd(therdUrl:any)
  {
    window.open(therdUrl)
  }
}



